<template>
  <div id="listItem">
    <div class="listItem">
      <div @click="isDone" class="inLine">
        <slot name="left">
          <svg v-if="!done" class="icon" aria-hidden="true"><use xlink:href="#icon-weiwancheng-"></use></svg>
          <svg v-else class="icon" aria-hidden="true"><use xlink:href="#icon-yiwancheng-"></use></svg>        
        </slot>        
      </div>
      <div @click="copyItem" class="inLine">      
        <slot name="center">
          <span v-if="done" class="center">{{item}}</span>
          <span v-else>{{item}}</span>                
        </slot>
      </div>
      <div @click="deleteItem" class="inLine">
        <slot name="right">
          <svg v-if="!done" class="icon right iconCenter" aria-hidden="true"><use xlink:href="#icon-shanchu1"></use></svg>
          <svg v-else class="icon right iconCenter" aria-hidden="true"><use xlink:href="#icon-shanchu"></use></svg>     
        </slot>        
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "ListItem",
    data() {
      return {
      }
    },
    props: {
      done: {
        type: Boolean,
        default: false,
      },
      item: String
    },
    methods: {
      isDone() {
        this.$emit('isDone')
      },
      deleteItem() {
        this.$emit('deleteItem')
      },
      copyItem() {
        this.$emit('copyItem')
      }
    }
  }
</script>

<style lang="less" scoped>
 .listItem {
    height: 0.2875rem;
    width: 99%;
    line-height: 0.2875rem;
    margin-bottom: 0.125rem;


    border-radius: 0.125rem;
    background-color: @item-bgc;
    box-shadow:@itemBoxShadow;

    span {
      padding-left: 0.1875rem;
    }
  }
  //字体图标
  .icon {
    width: 1.5em;
    height: 1.5em;
    vertical-align: -0.4em;
    fill: currentColor;
    overflow: hidden;
    padding-left: 0.1875rem;
    cursor: pointer;
  }
  .iconCenter {
    width: 1.4em;
    height: 1.4em;
    // vertical-align: 1.3rem;
    padding-top: 0.056rem;
    padding-right: 0.1875rem;
  }
  .center {
    text-decoration: line-through;
    color: #7A7A7A;
  }
</style>